/** 通用颜色设置 **/
html {
  font-size: 10px;
}
body {
  margin: 0;
  padding: 0;
  font-size: 1.4rem;
  min-width: 128.0rem;
  overflow: scroll;
  line-height: 2.0rem;
  background-color: #f6f6f6;
}
* {
  box-sizing: border-box;
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style-type: none;
}



